import Users from './users'
import { connect } from 'react-redux'
import {followUserTK,unFollowUserTK,setCurrentPage,setFollowingProgres, getUsersThunkCreator} from '../../redux/findUsers-reducer'
import { compose } from 'redux'
import { authRedirect } from '../HOC/auth-redirect'

const mapStateToProps = (state) => {
    return {
        usersData: state.findUsers.usersData,
        pageSize: state.findUsers.pageSize,
        totalUsers: state.findUsers.totalUsers,
        currentPage: state.findUsers.currentPage,
        isFetching: state.findUsers.isFetching,
        followingProgres: state.findUsers.followingProgres
    }
}
export default compose(
    connect(mapStateToProps,{followUserTK,unFollowUserTK,setCurrentPage,setFollowingProgres,getUsersThunkCreator}),
    authRedirect
)(Users)


