import './App.css';

import HeaderContainer1 from './components/header/headerContainer';
import Sidebar from './components/sideBar/SideBar';
import MainProfile from './components/main/profileContainer';
import DialogsContainer from './components/dialogs/DialogsContainer'
import News from './components/news/News';
import Music from './components/music/Music';
import Settings from './components/settings/Settings'
import { Route } from "react-router-dom"
import UsersContainer from './components/findUsers/usersContainer'
import Login from './common/preloader/login/login';


const App = (props) => {
    return (
        <div className="container">
            <div className="wrapper">
                <HeaderContainer1 />
                <Sidebar data={props.store.getState().sideBar} />
                <div className='main'>
                    <Route path="/dialogs" render={() => <DialogsContainer store={props.store} />} />
                    <Route path="/profile/:userid?" render={() => <MainProfile store={props.store} />} />
                    <Route path="/news" component={News} />
                    <Route path="/music" component={Music} />
                    <Route path="/settings" component={Settings} />
                    <Route path="/users" render={() => <UsersContainer store={props.store} />} />
                    <Route path="/login" render={() => <Login />} />
                </div>
            </div>
        </div>
    )
}


export default App;
