import NavItem from './navItem/NavItem'
import n from './nav.module.css'
const Nav = () => {
    return (
        <nav className={n.nav}>
          <ul className={n.list}>
            <NavItem where="/profile" text="Profile"/>
            <NavItem where="/dialogs" text="Messages"/>
            <NavItem where="/music" text="Music"/>
            <NavItem where="/news" text="News"/>
            <NavItem where="/settings" text="Settings"/>
            <NavItem where="/users" text="FindUsers"/>
          </ul>
        </nav>
    )
}

export default Nav;