import React from 'react';
import MainProfile from './mainProfile'

class ProfleContainer extends React.Component {
    componentDidMount() {
        if (!this.props.match.params.userid) {
            this.props.match.params.userid = 2
        }
        this.props.getProfile(this.props.match.params.userid)
        this.props.getStatus(this.props.match.params.userid)
    }
    
    render() {
        return <MainProfile data={this.props.profile} isAuth={this.props.isAuth} status={this.props.status} updateStatus={this.props.updateUserStatus}/>
    }
}


export default ProfleContainer;