import { authAPI } from "../API/api";

const SET_LOGIN_USER = 'SET_LOGIN_USER';

export const isLoginUser = (user) => {
    return {
        type:SET_LOGIN_USER,
        user
    }
}

const initialState = {
    userInfo :null,
    isAuth: false
}

const authReducer = (state = initialState,action) => {
    switch (action.type) {
        case SET_LOGIN_USER :
        return {
            ...state,
            userInfo: {...action.user.data},
            isAuth: true,
        }
        default : return state;
    }
}

export const auth = () => (dispatch) => {
    authAPI.isAuth().then(response => {
        if (!response.resultCode) {
            dispatch(isLoginUser(response))
        }
    })
}

export const login = (formdata) => (dispatch) => {
    authAPI.loginUser(formdata).then(response => {
        if (response.resultCode === 0) {
            dispatch(isLoginUser)
        } else {
            alert("mdaaa")
        }
    })
}

export default authReducer;