import { profileAPI } from "../API/api";

const ADD_POST = "ADD-POST"
const ADD_NEW_POST_TEXT = "ADD-NEW-POST-TEXT";
const UPDATE_USER = "UPDATE-USER";
const SET_STATUS = "SET-STATUS";
const UPDATE_STATUS = "UPDATE-STATUS";

export const addPostActionCreator = () => {
    return {
        type: ADD_POST,
    }
}

export const updatePostActionCreator = (text) => {
    return {
        type: ADD_NEW_POST_TEXT,
        text: text,
    }
}

const updateUser = (user) => {
    return {
        type: UPDATE_USER,
        user: user,
    }
}

const setStatus = (status) => {
    return {
        type: SET_STATUS,
        status
    }
}

const updateStatus = (status) => {
    return {
        type: UPDATE_STATUS,
        status
    }
}

let initialState = {
    profile: null,
    postData: {
        posts: [
            { id: 1, message: 'Hi how are you', likeCount: 5 },
            { id: 2, message: 'Hi how are you', likeCount: 5 },
            { id: 3, message: 'Hi ', likeCount: 5 }
        ],
        _newPostText: '',
    }
}

export const profileReducer = (state = initialState, action) => {


    switch (action.type) {
        case ADD_POST:
            let newPost = {
                id: 5,
                message: state._newPostText,
                likeCount: 0,
            };
            return {
                ...state,
                postData: [...state.postData, newPost],
                _newPostText: "",
            }
        case ADD_NEW_POST_TEXT:
            return {
                ...state,
                _newPostText: action.text
            }
        case UPDATE_USER:
            return {
                ...state,
                profile: {
                    contacts: { ...action.user.contacts },
                    photos: { ...action.user.photos },
                    status: action.user.aboutMe,
                }

            }
        case UPDATE_STATUS:
            return {
                ...state,
                status: action.status
            }
        case SET_STATUS:
            return {
                ...state,
                status: action.status
            }
        default: return state;
    }
}

export const getProfile = (id) => (dispatch) => {
    profileAPI.getProfile(id)
        .then(response => {  dispatch(updateUser(response)) })
}

export const getStatus = (id) => (dispatch) => {
    profileAPI.getStatus(id).then(response => { dispatch(setStatus(response)) })
}

export const updateUserStatus = (status) => (dispatch) => {
    profileAPI.updateStatus(status).then(response => {
        if (!response.resultCode) {
            dispatch(updateStatus(status))
        }
    })
}



export default profileReducer;