import d from './Dialogs.module.css'
import Dialog from './dialog/Dialog'
import Messages from './message/Message'
import React from 'react';


class Dialogs extends React.Component {

     state = {
        message: "",
        messages:[...this.props.messageData],
    }
    
    onChangeNewMess = (e) => {
        this.setState({message: e.target.value});
    }

    componentDidUpdate = (prevProps,prevState) => {
        if (prevState.message !== this.state.message) {
            this.setState({message:this.state.message})
        }
        debugger

        if (prevProps.messageData.length !== this.state.messages.length) {
            this.setState({messages:[...this.props.messageData]})
        }
    }
    
    addMessage = () => {
        this.props.addMessage(this.state.message)
        debugger
    }

    dialogs = this.props.messageName.map((el) => <Dialog name={el.name} key={el.id} id={el.id} />)

    messageElement = this.state.messages.map(el => <Messages text={el.message} key={el.id}/>)
    
    render() {
        return (
            <div className={d.wrapper}>
                <div className={d.dialogs}>
                    {this.dialogs}
                </div>
                <div className={d.messages}>
                    {this.messageElement}
                    <textarea onChange={this.onChangeNewMess} value={this.state.message}></textarea>
                    <button onClick={this.addMessage}>Отправить</button>
                </div>
                
            </div>
        )
    }
}

export default Dialogs