import { usersAPI } from "../API/api";



const FOLLOW_USER = "FOLLOW_USER";
const UNFOLLOW_USER = "UNFOLLOW_USER";
const ADD_USERS = "ADD_USERS";
const SET_CURRENT_PAGE ="SET_CURRENT_PAGE"
const SET_TOTAL_COUNT = "SET_TOTAL_COUNT"
const FETCHING = "FETCHING";
const FOLLOW_FETCHING = "FOLLOW_FETCHING";

export const followUser = (id) => {
    return {
        type : FOLLOW_USER,
        id: id
    }
}

export const unFollowUser = (id) => {
    return {
        type : UNFOLLOW_USER,
        id: id
    }
}

export const showMoreUser = (users) => {
    return {
        type : ADD_USERS,
        arrUsers: [...users]
    }
}

export const setCurrentPage = (page) => {
    return {
        type: SET_CURRENT_PAGE,
        currentPage: page
    }
}

export const setTotalUsers = (users) => {
    return {
        type : SET_TOTAL_COUNT,
        totalUsers: users
    }
}

export const setFetching = (bool) => {
    return {
        type: FETCHING,
        fetching: bool
    }
}

export const setFollowingProgres = (id,bool) => {
    return {
        type: FOLLOW_FETCHING,
        id: id,
        isFetching:bool
    }
}

let initialState = {
    usersData: [],
    pageSize: 5, 
    totalUsers: 0,
    currentPage: 2,
    isFetching: false,
    followingProgres: []
}

const findUsersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW_USER:
            return {
                ...state,
                usersData: state.usersData.map(user => {
                    if (user.id === action.id) {
                        user.followed = true
                    }
                    return user;
                })
            }
        case UNFOLLOW_USER:
            return {
                ...state,
                usersData: state.usersData.map(user => {
                    if (user.id === action.id) {
                        user.followed = false
                    }
                    return user;
                })
            }
        case ADD_USERS:
            return {
                ...state,
                usersData: [...action.arrUsers]
            }
        case SET_CURRENT_PAGE:
            return {
                ...state,
                currentPage: action.currentPage
            }
        case SET_TOTAL_COUNT:
            return {
                ...state,
                totalUsers: action.totalUsers
            }
        case FETCHING:
            return {
                ...state,
                isFetching: action.fetching
            }
            case FOLLOW_FETCHING:
                return {
                    ...state,
                    followingProgres: action.isFetching ? [...state.followingProgres,action.id]:state.followingProgres.filter(id => id !== action.id)
                }
        default: return state
    }
}

export const getUsersThunkCreator = (currentPage,pageSize) => (dispatch) => {
    dispatch(setFetching(true));
        usersAPI.getUsers(currentPage,pageSize).then(respone => {
            dispatch(setFetching(false))
            dispatch(setTotalUsers(respone.totalCount));
            dispatch(showMoreUser(respone.items));
        });
}

export const followUserTK = (id) => (dispatch) => {
    usersAPI.followUser(id).then((response) => {
        if (response.resultCode === 0) {
            dispatch(followUser(id))
        }
        dispatch(setFollowingProgres(id,false))
    })
}

export const unFollowUserTK = (id) => (dispatch) => {
    usersAPI.followUser(id).then((response) => {
        if (response.resultCode === 0) {
            dispatch(unFollowUser(id))
        }
        dispatch(setFollowingProgres(id,false))
    })
}

export default findUsersReducer;

