import { NavLink } from 'react-router-dom';
import HeaderStyle from './Header.module.css';



const Header = (props) => {
    return ( 
    <header className = {HeaderStyle.header} >
        {props.isAuth ? <div><span>{props.userInfo.login}</span>
            <span>{props.userInfo.email}</span></div> : <div><NavLink to="/login">Log in</NavLink></div> }
        </header>
    )
}

export default Header;