import f from './friend.module.css'
import { NavLink } from "react-router-dom"

const Friend = (props) => {
    return (
        <div className={f.wrapper}>
            <NavLink to="#2"className={f.friend} activeClassName={f.myActive}>
                <img className={f.avatar} src="https://img1.freepng.ru/20180315/wre/kisspng-computer-icons-clip-art-man-icon-5aab18530bdfb1.3779945315211623230486.jpg" alt="" />
                <span className={f.name}>{props.dataFriend}</span>
            </NavLink>
        </div>
    )
}   

export default Friend;