import b from './bestFriend.module.css'
import Friend from './friend/Friend'

const BestFriends = (props) => {

    let BestFriendsList = props.data.map(el => <Friend dataFriend={el.name}/>)
    return (
        <div className={b.wrapper}>
            {BestFriendsList}
        </div>
    )
}

export default BestFriends;