import User from "./user/user"
import *  as axios from 'axios';
import React from 'react';
import u from "./users.module.css"
import Preloader from "../../common/preloader/preloader"
import { usersAPI } from "../../API/api";


class Users extends React.Component {


    componentDidMount() {
        this.props.getUsersThunkCreator(this.props.currentPage,this.props.pageSize)
    }


    getArrUsers = () => {
        return this.props.usersData.map(user => {
            return <User data={user} followUser={this.props.followUserTK} unFollowUser={this.props.unFollowUserTK} setFollowingProgres={this.props.setFollowingProgres} followingProgres={this.props.followingProgres}/>
        })
    }

    onPageChange = (page) => {
        this.props.getUsersThunkCreator(page,this.props.pageSize);
        this.props.setCurrentPage(page);
    }

    render() {
        let pagesCount = Math.ceil(this.props.totalUsers / this.props.pageSize);
        let pages = [];
        for (let i = 1; i <= pagesCount; i++) {
            pages.push(i);
        }
        return <div>
            {this.props.isFetching ?
            <Preloader/>:null}
            <div className="">
                <div className={u.pages}>
                {pages.map(el => <span className={el === this.props.currentPage ? u.selected: "q"} onClick={() => {this.onPageChange(el)}}>{el}</span>)}
                </div>
                {this.getArrUsers()}
                <button >Show More</button>
            </div>
        </div>
    }
}


export default Users