import React from "react"

class  ProfileStatus extends React.Component {

    // statusInput = React.createRef()

    state = {
        editMode: false,
        status: "",
    }
    activateEditMode = () => {
        this.setState({editMode: true})
    }

    componentDidUpdate = (prevProps, prevState) => {
        if (prevState.status !== this.props.status) {
            this.setState({status: this.props.status})            
        }
    }

    onStatusChange = (e) => {
        this.setState({
            status: e.target.value
        })
    }

    deactivateEditMode = (status) => {
        this.setState({editMode: false})
        if (this.state.status.length < 300) {
            this.props.updateStatus(this.state.status)       
        } else {
            alert("Введено слишком много символов")
        }
    }
     render() {
        return (
        <div> 
            {!this.state.editMode &&
                <div className="{p.description}">
                    <span onDoubleClick={this.activateEditMode}>{this.props.status}</span>
                </div>
            }
            {this.state.editMode &&
                <div>
                    <input onChange={this.onStatusChange} onBlur={this.deactivateEditMode} value={this.state.status || 1} autoFocus />
                    <button></button>
                </div>
            }
        </div>
        )
    }
}

export default ProfileStatus