import u from "./user.module.css";
import { NavLink } from "react-router-dom"



const User = (props) => {
    const onclickUser = () => {
        props.setFollowingProgres(props.data.id,true)
        if (!props.data.followed) {
            props.followUser(props.data.id)       
        } else {
            props.unFollowUser(props.data.id)
        }
    }

    return (
        <div className={u.wrapper}>
            <div className={u.leftBlock}>
                <div className={u.avatar}>
                    <NavLink to={'/profile/' + props.data.id}>
                        <img src={props.data.photos.small !== null ? props.data.photos.small : "https://img1.freepng.ru/20180315/wre/kisspng-computer-icons-clip-art-man-icon-5aab18530bdfb1.3779945315211623230486.jpg"} alt="" />
                    </NavLink>
                </div>
                <button onClick={onclickUser} disabled={props.followingProgres.some(id => id === props.data.id)}>{!props.data.followed ? "Follow" : "Unfollow"}</button>
            </div>
            <div className={u.rightBlock}>
                <div>
                    <span>{props.data.name}</span>
                    <span>{props.data.status}</span>
                </div>
                <div>
                    <span>{"props.data.location.town"}</span>
                    <span>{"props.data.location.country"}</span>
                </div>
            </div>
        </div>
    )
}

export default User

